export default {
    //se manejan estados referente a data global
    namespaced: true,

    state: {
        vehiculos:[],
        usuarios:[],
        viajes:[]
    },

    mutations: {
        SET_VEHICULOS(state,payload){
            state.vehiculos = payload;
        },
        SET_USUARIOS(state,payload){
            state.usuarios = payload;
        },
        SET_VIAJES(state,payload){
            state.viajes = payload;
        },
        PUSH_VIAJE(state,payload){
            state.viajes.push(payload);
        }
    },
  
    actions: {
        setVehiculos({commit},payload){
            commit('SET_VEHICULOS',payload);
        },
        setUsuarios({commit},payload){
            commit('SET_USUARIOS',payload);
        },
        setViajes({commit},payload){
            commit("SET_VIAJES",payload);
        },
        pushViaje({commit},payload){
            commit("PUSH_VIAJE",payload);
        }
    }
}